import React, {Component} from "react";
import _ from "lodash";
export default class SimCartIndexHeader extends Component{
     constructor (props) {
            super(props);
    }
    getText(inx){
      switch (inx) {
          case 0:
              return "Pierwsza";
          case 1:
              return "Druga";
          case 2:
              return "Trzecia";
          case 3:
              return "Czwarta";
          case 4:
              return "Piąta";
          case 5:
              return "Szósta";
          case 6:
              return "Siódma";
          case 7:
              return "Ósma";
          case 8:
              return "Dziewiąta";
          case 9:
              return "Dziesiąta";
          case 10:
              return "Jedenasta";
          case 11:
              return "Dwunasta";
          case 12:
              return "Trzynasta";
          case 13:
              return "Czternasta";
          case 14:
              return "Piętnasta";
          case 15:
              return "Szesnasta";
          case 16:
              return "Siedemnasta";
          case 17:
              return "Osiemnasta";
          case 18:
              return "Dziewiętnasta";
          case 19:
              return "Dwudziesta";
          case 29:
              return "Trzydziesta";
          case 39:
              return "Czterdziesta";
          case 49:
              return "Pięćdziesiąta";
          case 59:
              return "Sześćdziesiąta";
          case 69:
              return "Siedemdziesiąta";
          case 79:
              return "Osiemdziesiąta";
          case 89:
              return "Dziewięćdziesiąta";
          default:
              return null;
        }
    }

    index2Text(inx)
    {
        if(inx < 20 || inx % 10 === 9) {
            return this.getText(inx)
        } else {
            let rest = inx % 10;
            return this.getText(inx - rest - 1) + ' ' +  _.lowerFirst(this.getText(rest))
        }
    }

    render(){
        return (
        <p className={"h3 " +this.props.className}>
            {this.index2Text(this.props.simIndex)} karta
        </p>)

    }

}